import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { useState, useEffect } from 'react';
import { LoadingSpinner } from '../common/LoadingSpinner';

const MainContent = styled.main`
  min-height: 100vh;
  background: ${props => props.theme.colors.primary};
  position: relative;
  overflow-x: hidden;
  padding: 0;
  width: 100%;
  
  @media (max-width: 768px) {
    padding-top: ${props => props.theme.spacing['4xl']};
  }
  
  &::before {
    content: '';
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: radial-gradient(circle at 70% 20%, rgba(0, 255, 231, 0.03) 0%, transparent 50%),
                radial-gradient(circle at 30% 80%, rgba(255, 0, 200, 0.03) 0%, transparent 50%);
    pointer-events: none;
  }
`;

const Section = styled(motion.section)`
  max-width: 1200px;
  margin: 0 auto;
  padding: ${props => props.theme.spacing.md};
  
  @media (max-width: 768px) {
    padding: ${props => props.theme.spacing.md};
  }

  &.loading {
    opacity: 0.5;
    pointer-events: none;
  }
`;

const SectionHeader = styled.div`
  margin-bottom: ${props => props.theme.spacing['2xl']};
  position: relative;
  z-index: ${props => props.theme.zIndex.fixed};
  
  h1 {
    font-size: ${props => props.theme.typography.fontSize['5xl']};
    font-weight: ${props => props.theme.typography.fontWeight.bold};
    color: ${props => props.theme.colors.text.primary};
    margin-bottom: ${props => props.theme.spacing.md};
    position: relative;
    z-index: ${props => props.theme.zIndex.fixed};
    
    span {
      color: ${props => props.theme.colors.text.accent};
    }
  }
  
  p {
    font-size: ${props => props.theme.typography.fontSize.xl};
    color: ${props => props.theme.colors.text.secondary};
    max-width: 600px;
    position: relative;
    z-index: ${props => props.theme.zIndex.fixed};
  }
  
  /* Mobile typography optimization */
  @media (max-width: 768px) {
    margin-bottom: ${props => props.theme.spacing.xl};
    
    h1 {
      font-size: ${props => props.theme.typography.fontSize['3xl']};
      line-height: 1.2;
      margin-bottom: ${props => props.theme.spacing.lg};
    }
    
    p {
      font-size: ${props => props.theme.typography.fontSize.lg};
      line-height: 1.5;
      max-width: 100%;
    }
  }
`;

const TopBranding = styled.div`
  position: fixed;
  top: ${props => props.theme.spacing.lg};
  right: ${props => props.theme.spacing.lg};
  z-index: ${props => props.theme.zIndex.fixed};
  text-align: right;
  pointer-events: none; /* Ensure it doesn't interfere with navigation */
  
  .author {
    font-weight: ${props => props.theme.typography.fontWeight.semibold};
    font-size: ${props => props.theme.typography.fontSize.md};
    color: ${props => props.theme.colors.text.primary};
    margin-bottom: ${props => props.theme.spacing.xs};
  }
  
  .company {
    font-size: ${props => props.theme.typography.fontSize.sm};
    color: ${props => props.theme.colors.text.accent};
    opacity: 0.9;
  }
  
  /* Mobile optimizations */
  @media (max-width: 768px) {
    top: ${props => props.theme.spacing['4xl']};
    right: ${props => props.theme.spacing.md};
    
    .author {
      font-size: ${props => props.theme.typography.fontSize.sm};
      margin-bottom: 2px;
    }
    
    .company {
      font-size: ${props => props.theme.typography.fontSize.xs};
    }
  }
`;

const LockOverlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 10;
  color: ${props => props.theme.colors.text.primary};
  text-align: center;
  padding: ${props => props.theme.spacing.xl};
  
  svg {
    width: 60px;
    height: 60px;
    margin-bottom: ${props => props.theme.spacing.lg};
    color: ${props => props.theme.colors.text.accent};
  }
  
  h2 {
    font-size: ${props => props.theme.typography.fontSize.xl};
    margin-bottom: ${props => props.theme.spacing.md};
  }
  
  p {
    font-size: ${props => props.theme.typography.fontSize.md};
    max-width: 600px;
    opacity: 0.8;
    line-height: 1.6;
  }
`;

// Animation variants
const pageVariants = {
  initial: { opacity: 0, x: 20 },
  enter: { 
    opacity: 1, 
    x: 0,
    transition: {
      duration: 0.5,
      ease: [0.4, 0, 0.2, 1]
    }
  },
  exit: { 
    opacity: 0, 
    x: -20,
    transition: {
      duration: 0.3,
      ease: [0.4, 0, 0.2, 1]
    }
  }
};

const Presentation = ({ sections, currentSection, sidebarWidth = 280, isContentUnlocked = false }) => {
  const [isLoading, setIsLoading] = useState(true);
  
  useEffect(() => {
    setIsLoading(true);
    const timer = setTimeout(() => setIsLoading(false), 500);
    return () => clearTimeout(timer);
  }, [currentSection]);
  
  // Check if the current section is locked
  const currentSectionData = sections[currentSection];
  const isSectionLocked = currentSectionData?.part > 1 && !isContentUnlocked;
  
  return (
    <MainContent $sidebarWidth={sidebarWidth}>
      <TopBranding>
        <div className="author">Leon Chlon</div>
        <div className="company">Hassana Labs</div>
      </TopBranding>
      
      <AnimatePresence mode="wait">
        <Section
          key={sections[currentSection].id}
          className={isLoading ? 'loading' : ''}
          initial="initial"
          animate="enter"
          exit="exit"
          variants={pageVariants}
        >
          {isLoading ? (
            <LoadingSpinner />
          ) : (
            <>
              <SectionHeader>
                <h1>
                  <span>{sections[currentSection].icon}</span>
                  {sections[currentSection].title}
                </h1>
                <p>{sections[currentSection].description}</p>
              </SectionHeader>
              
              {/* Section-specific content */}
              {isSectionLocked ? (
                <LockOverlay>
                  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" d="M16.5 10.5V6.75a4.5 4.5 0 10-9 0v3.75m-.75 11.25h10.5a2.25 2.25 0 002.25-2.25v-6.75a2.25 2.25 0 00-2.25-2.25H6.75a2.25 2.25 0 00-2.25 2.25v6.75a2.25 2.25 0 002.25 2.25z" />
                  </svg>
                  <h2>Content Locked</h2>
                  <p>
                    This section is password-protected. Please navigate back to Part 1 
                    or use the navigation menu to access unlocked content.
                  </p>
                </LockOverlay>
              ) : (
                sections[currentSection].component
              )}
            </>
          )}
        </Section>
      </AnimatePresence>
    </MainContent>
  );
};


export default Presentation;